<template>
  <section class="services" id="services">
    <div class="container">
      <div class="wrap aic">
        <div class="col-services">
          <h2 class="services-title">Services</h2>
          <p class="services-desc">Simplify the daily operations of your business with our comprehensive range of services. Our goal is to provide you with the support you need to keep your business running smoothly.</p>
        </div>
      </div>
      <div class="wrap aic">
        <div class="col-services-card">
          <div 
            class="services-card"  
            @mousemove="handleMouseMove(service.id, $event)"
            @mouseleave="handleMouseLeave" 
            v-for="service in services" 
            :key="service.id"
            :style="getCardStyle(service.id)">
            <img class="services-card-svg" :src="service.img" alt="Services SVG">
            <div class="service-card-body">
              <h3 class="service-card-title">{{ service.title }}</h3>
              <p class="service-card-desc">{{ service.desc }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import vscode from '../assets/images/vscode.svg'
import maintenance from '../assets/images/maintenance.svg'
import terminal from '../assets/images/terminal.svg'
import { ref } from 'vue';

export default {
  data() {
    return {
      services: [
        {
          id: 1,
          img: vscode,
          title: 'Web Development',
          desc: 'Our Front-End Development is pixel perfect in all ways.'
        },
        {
          id: 2,
          img: maintenance,
          title: 'Maintenance',
          desc: 'IT support and system maintenance services.'
        },
        {
          id: 3,
          img: terminal,
          title: 'Debugging',
          desc: 'We offer assistance for your IT issues.'
        }
      ]
    }
  },
  setup() {
    const activeCard = ref(null);
    const x = ref(0);
    const y = ref(0);

    const getCardStyle = (id) => {
      if (activeCard.value === id) {
        return {
          transform: `perspective(1000px) rotateX(${y.value}deg) rotateY(${x.value}deg)`,
          transition: 'transform 0.2s ease',
          willChange: 'transform'
        }
      }
      return {
        transform: 'perspective(1000px) rotateX(0deg) rotateY(0deg)',
        transition: 'transform 0.2s ease'
      }
    }

    const handleMouseMove = (id, event) => {
      activeCard.value = id;
      const card = event.currentTarget;
      const rect = card.getBoundingClientRect();
      const mouseX = event.clientX - rect.left;
      const mouseY = event.clientY - rect.top;

      const centerX = rect.width / 2;
      const centerY = rect.height / 2;

      const rotateX = ((mouseY - centerY) / centerY) * 10;
      const rotateY = ((mouseX - centerX) / centerX) * -10;

      x.value = rotateY;
      y.value = rotateX;
    };

    const handleMouseLeave = () => {
      activeCard.value = null;
      x.value = 0;
      y.value = 0;
    };

    return {
      activeCard,
      getCardStyle,
      handleMouseMove,
      handleMouseLeave
    }
  }
}
</script>

