<template>
  <section class="sectiontwo" id="sectiontwo">
    <div class="container">
      <div class="wrap aic">
        <div class="about-us-col">
          <h2 class="about-us-title">Everything you need to know.</h2>
          <p class="about-us-text">
            We are a team of developers who are passionate about what we do. We are here to help you with your IT needs
            and by that you help many families and people in need ❤️.
          </p>
        </div>
      </div>
      <div class="wrap">
        <div class="col-about-cards">
          <!-- Loop through cards, and add mouse events for each card -->
          <div 
            class="cards-about" 
            v-for="card in cards" 
            :key="card.id"
            @mousemove="handleMouseMove(card.id, $event)"
            @mouseleave="handleMouseLeave"
            :style="getCardStyle(card.id)">
            <img :src="require(`../assets/images/${card.img}`)" :alt="card.title">
            <div class="card-body">
              <h3 class="card-body-title">{{ card.title }}</h3>
              <p class="card-body-desc">{{ card.desc }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { ref } from 'vue';

export default {
  name: 'SectionTwo',
  data() {
    return {
      cards: [
        {
          id: 1,
          img: 'orph.jpg',
          title: 'Orphans',
          desc: 'We support orphans by providing them with education, clothes, food and emotional care to ensure they have a bright future.'
        },
        {
          id: 2,
          img: 'families.jpg',
          title: 'Families',
          desc: 'We assist families in need by offering financial aid, food supplies, and access to essential services to improve their living conditions.'
        },
        {
          id: 3,
          img: 'homeless.jpg',
          title: 'Homeless',
          desc: 'We provide essential support and resources to help homeless people find shelter, food, and a path to a better life.'
        }
      ]
    }
  },
  setup() {
    const activeCard = ref(null);
    const x = ref(0);
    const y = ref(0);

    // Function to get the card style based on mouse position
    const getCardStyle = (id) => {
      if (activeCard.value === id) {
        return {
          transform: `perspective(1000px) rotateX(${y.value}deg) rotateY(${x.value}deg)`,
          transition: 'transform 0.2s ease',
          willChange: 'transform'
        }
      }
      return {
        transform: 'perspective(1000px) rotateX(0deg) rotateY(0deg)',
        transition: 'transform 0.2s ease'
      }
    }

    // Handle mouse movement over a card
    const handleMouseMove = (id, event) => {
      activeCard.value = id; // Set active card on mouse move
      const card = event.currentTarget;
      const rect = card.getBoundingClientRect();
      const mouseX = event.clientX - rect.left;
      const mouseY = event.clientY - rect.top;

      const centerX = rect.width / 2;
      const centerY = rect.height / 2;

      const rotateX = ((mouseY - centerY) / centerY) * 10;
      const rotateY = ((mouseX - centerX) / centerX) * -10;

      x.value = rotateY;
      y.value = rotateX;
    };

    // Reset styles when the mouse leaves the card
    const handleMouseLeave = () => {
      activeCard.value = null;
      x.value = 0;
      y.value = 0;
    };

    return {
      activeCard,
      getCardStyle,
      handleMouseMove,
      handleMouseLeave
    }
  }
}
</script>

