<template>
  <div class="sidebar-dnone">
    <div class="burger" @click="toggleSidebar">
      <span class="burger-line"></span>
      <span class="burger-line"></span>
      <span class="burger-line"></span>
    </div>
    <!-- Sidebar -->
    <div :class="{'sidebar': true, 'sidebar-open': isSidebarOpen}">
      <ul class="sidebar-menu">
        <li class="menu-item">
          <router-link to="/">
            Home
          </router-link>
        </li>
        <li class="menu-item">
          <a class="nav-a" @click="scrollToElement('#services')">Services</a>
        </li>
        <li class="menu-item">
          <router-link to="/contact">
            Contact
          </router-link>
        </li>
        <li class="menu-item">
          <a class="getsidebar" href="#">Get Started</a>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: 'SideBar',
  data() {
    return {
      isSidebarOpen: false
    }
  },
  methods: {
    toggleSidebar() {
      this.isSidebarOpen = !this.isSidebarOpen
    },
    handleScroll() {
      if(this.isSidebarOpen) {
        this.isSidebarOpen = false
      }
    },
    scrollToElement(selector) {
      const element = document.querySelector(selector);
      if (element) {
        element.scrollIntoView({
          behavior: 'smooth',  // Smooth scrolling
          block: 'start',      // Aligns the top of the element to the top of the viewport
        });
      }
    },
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeUnmount() {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>
